B,kEAAkE;AACpE;;AAEA,uBAAuB;AACvB;EACE,OAAO,UAAU,EAAE;EACnB,KAAK,UAAU,EAAE;AACnB;;AAEA;EACE,kCAAkC;AACpC;;AAEA,oBAAoB;AACpB;EACE,iEAAiE;EACjE,gCAAgC;EAChC,oCAAoC;EACpC,+CAA+C;AACjD;;AAEA;EACE,oEAAoE;EACpE,sFAAsF;AACxF;;AAEA,qBAAqB;AACrB;EACE,6BAA6B;EAC7B,8EAA8E;AAChF;;AAEA,eAAe;AACf;EACE,aAAa;EACb,4DAA4D;EAC5D,WAAW;EACX,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,wDAAwD;EACxD,kBAAkB;EAClB,gBAAgB;EAChB,mEAAmE;AACrE;;AAEA;EACE,aAAa;EACb,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,6DAA6D;EAC7D,uDAAuD;AACzD;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,6DAA6D;EAC7D,qBAAqB;EACrB,wDAAwD;AAC1D;;AAEA;EACE,iEAAiE;EACjE,wDAAwD;EACxD,+EAA+E;EAC/E,uDAAuD;EACvD,uBAAuB;EACvB,kBAAkB;EAClB,qBAAqB;AACvB;;AAEA;EACE,iEAAiE;EACjE,wDAAwD;AAC1D;;AAEA,eAAe;AACf;EACE,aAAa;EACb,wDAAwD;EACxD,kBAAkB;EAClB,gBAAgB;EAChB,mEAAmE;EACnE,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,aAAa;EACb,OAAO;AACT;;AAEA;EACE,6DAA6D;EAC7D,qBAAqB;EACrB,wDAAwD;AAC1D;;AAEA;EACE,iEAAiE;EACjE,wDAAwD;EACxD,+EAA+E;EAC/E,uDAAuD;EACvD,uBAAuB;EACvB,kBAAkB;EAClB,qBAAqB;EACrB,qBAAqB;AACvB;;AAEA;EACE,iEAAiE;EACjE,wDAAwD;AAC1D","sourcesContent":["/* 스토리북 UI에 Fluent 2 스타일 적용 */\n\n/* 기본 폰트 패밀리 설정 */\nbody {\n  font-family: 'Pretendard', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n  background-color: var(--foundation-color-grey-light-100);\n  color: var(--semantic-color-secondary-foreground-1-rest);\n}\n\n/* Fluent 2 스타일의 기본 레이아웃 */\n#storybook-root {\n  background-color: var(--foundation-color-grey-light-100);\n}\n\n/* 사이드바 스타일 재정의 */\n.sidebar-container {\n  background-color: var(--foundation-color-grey-light-98) !important;\n  border-right: 1px solid var(--foundation-color-grey-light-90) !important;\n}\n\n.sidebar-header {\n  border-bottom: 1px solid var(--foundation-color-grey-light-90) !important;\n  padding: 1.5rem 1rem !important;\n}\n\n.sidebar-header svg {\n  width: 30px !important;\n  height: 30px !important;\n}\n\n/* 스토리북 스토리 컨테이너 */\n.sb-story-container {\n  margin: 2rem;\n  background-color: var(--foundation-color-grey-light-100);\n  border-radius: 8px;\n  box-shadow: 0 2px 8px var(--foundation-color-transparent-light-100);\n}\n\n/* 스토리북 문서 컨테이너 */\n.sb-docs-container {\n  font-family: 'Pretendard', sans-serif;\n  background-color: var(--foundation-color-grey-light-100);\n  border-radius: 8px;\n  padding: 2rem !important;\n}\n\n/* 스토리북 메인 영역 */\n.sb-show-main {\n  color: var(--semantic-color-secondary-foreground-1-rest);\n  background: var(--foundation-color-grey-light-98) !important;\n  padding: 1rem !important;\n}\n\n/* DocsPage 타이틀 - hero/1 bold */\n.sbdocs-title {\n  font-size: var(--semantic-typography-hero\\/1-bold-fontSize);\n  line-height: var(--semantic-typography-hero\\/1-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-hero\\/1-bold-letterSpacing);\n  font-weight: var(--semantic-typography-hero\\/1-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-1-rest);\n  margin-bottom: var(--semantic-spacingVer-global-md);\n  border-bottom: none !important;\n}\n\n/* DocsPage 서브타이틀 - title/1 bold */\n.sbdocs-h2 {\n  font-size: var(--semantic-typography-title\\/1-bold-fontSize);\n  line-height: var(--semantic-typography-title\\/1-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-title\\/1-bold-letterSpacing);\n  font-weight: var(--semantic-typography-title\\/1-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-1-rest);\n  border-bottom: 1px solid var(--foundation-color-grey-light-90);\n  padding-bottom: var(--semantic-spacingVer-global-xs);\n  margin-top: var(--semantic-spacingVer-global-xl);\n  margin-bottom: var(--semantic-spacingVer-global-sm);\n}\n\n/* DocsPage 섹션 헤더 - heading/1 bold */\n.sbdocs-h3 {\n  font-size: var(--semantic-typography-heading\\/1-bold-fontSize);\n  line-height: var(--semantic-typography-heading\\/1-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-heading\\/1-bold-letterSpacing);\n  font-weight: var(--semantic-typography-heading\\/1-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-1-rest);\n  margin-top: var(--semantic-spacingVer-global-lg);\n  margin-bottom: var(--semantic-spacingVer-global-xs);\n}\n\n/* DocsPage 서브섹션 헤더 - heading/2 bold */\n.sbdocs-h4 {\n  font-size: var(--semantic-typography-heading\\/2-bold-fontSize);\n  line-height: var(--semantic-typography-heading\\/2-bold-lineHeight);\n  letter-spacing: var(--semantic-typography-heading\\/2-bold-letterSpacing);\n  font-weight: var(--semantic-typography-heading\\/2-bold-fontWeight);\n  color: var(--semantic-color-secondary-foreground-2-rest);\n  margin-top: var(--semantic-spacingVer-global-md);\n  margin-bottom: var(--semantic-spacingVer-global-xxs);\n}\n\n/* DocsPage 단락 텍스트 - body/1 regular */\n.sbdocs-p {\n  font-size: var(--semantic-typography-body\\/1-regular-fontSize);\n  line-height: var(--semantic-typography-body\\/1-regular-lineHeight);\n  letter-spacing: var(--semantic-typography-body\\/1-regular-letterSpacing);\n  font-weight: var(--semantic-typography-body\\/1-regular-fontWeight);\n  color: var(--semantic-color-secondary-foreground-2-rest);\n  margin-bottom: var(--semantic-spacingVer-global-sm);\n}\n\n/* DocsPage 코드 블록 - body/2 regular */\n.docblock-code-toggle, \n.docblock-source {\n  font-size: var(--semantic-typography-body\\/2-regular-fontSize);\n  line-height: var(--semantic-typography-body\\/2-regular-lineHeight);\n  font-family: 'SF Mono', 'Menlo', 'Monaco', 'Consolas', 'Ubuntu Mono', monospace;\n  background-color: var(--foundation-color-grey-light-96) !important;\n  border-radius: 8px !important;\n  border: 1px solid var(--foundation-color-grey-light-90) !important;\n}\n\n/* DocsPage 코드 인라인 - body/2 regular */\n.sbdocs-code, \n.sbdocs-p code {\n  font-size: var(--semantic-typography-body\\/2-regular-fontSize);\n  font-family: 'SF Mono', 'Menlo', 'Monaco', 'Consolas', 'Ubuntu Mono', monospace;\n  background-color: var(--foundation-color-grey-light-96);\n  color: var(--s